body {
  margin: 0px;
  padding: 0px;
  overflow: hidden; }

#map {
  width: 100%;
  height: 100vh;
  position: fixed;
  z-index: 1; }

#editButton {
  display: block;
  background-color: #ff5e3a;
  color: #FFF;
  font-weight: bold;
  padding: 10px;
  border-radius: 5px;
  position: absolute;
  right: 25px;
  bottom: 25px;
  z-index: 2; }

.menuButton {
  display: block;
  width: 50px;
  height: 50px;
  background-color: #ff5e3a;
  position: absolute;
  left: 0;
  top: 0;
  z-index: 10; }

.menuButton span,
.menuButton span::before,
.menuButton span::after {
  position: absolute;
  top: 50%;
  margin-top: -1px;
  left: 50%;
  margin-left: -10px;
  width: 20px;
  height: 2px;
  background-color: #272e4f; }

.menuButton span::before,
.menuButton span::after {
  content: '';
  display: block;
  -webkit-transition: 0.2s;
  -o-transition: 0.2s;
  transition: 0.2s; }

.menuButton span::before {
  -webkit-transform: translateY(-5px);
      -ms-transform: translateY(-5px);
          transform: translateY(-5px); }

.menuButton span::after {
  -webkit-transform: translateY(5px);
      -ms-transform: translateY(5px);
          transform: translateY(5px); }

.menuButton_active span:before {
  -webkit-transform: rotate(45deg) translateX(0px);
      -ms-transform: rotate(45deg) translateX(0px);
          transform: rotate(45deg) translateX(0px);
  width: 20px; }

.menuButton_active span:after {
  -webkit-transform: rotate(-45deg) translateX(0px);
      -ms-transform: rotate(-45deg) translateX(0px);
          transform: rotate(-45deg) translateX(0px);
  width: 20px; }

.menuButton_active span {
  background-color: #ff5e3a; }

